import React from 'react';
import { Routes, Route, useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import './styles/reflection.scss';
import { Tooltip } from 'react-tooltip';
import Carousel from 'react-elastic-carousel';

export default function Reflection() {
    let navigate = useNavigate();
    let params = useParams();
    const mood = params.mood;

    const breakPoints = [
      { width: 1, itemsToShow: 1 },
      { width: 550, itemsToShow: 1},
      { width: 768, itemsToShow: 1}
    ];

    let items= [
      {question: 'item #1'},
      {question: 'item #2'},
  ]

    const happyRecomm = [
      {

      }
    ]

    const sadRecomm = [
      {
          
      }
    ]

    const stressedRecomm = [
      {
          
      }
    ]

    const lonelyRecomm = [
      {
          
      }
    ]

    const exploreRecomm = [
      {
          
      }
    ]


    function goToExplore() {
      navigate('/explore');
    }

    function getDate() {
      let myDate = new Date();
      let day = myDate.getDate();
      let month = myDate.getMonth() + 1;

      if (month < 10) {
        month = "0" + month.toString();
      }

      let date = day.toString() + month;

      return date;
    }

    function downloadTxtFile() {
      let text = "";
      let element = document.createElement("a");
      let questions = document.querySelectorAll(".legend");
      questions.forEach((question, index) => {
        let questionText = question.innerHTML;
        let answer = document.getElementById('input-' + index).value;
        text = text + questionText + "\n" + answer + "\n\n";
      });

      let file = new Blob([text], {type: 'text/plain'});
      element.href = URL.createObjectURL(file);
      element.download = "answers" + getDate() + ".txt";
      document.body.appendChild(element); // Required for this to work in FireFox
      element.click();
    }

    function goBack() {
      navigate(-1);
    }

    function goToHomepage() {
      navigate('/Wellbeing-website');
    }

    return (
        <div className="reflection-container">
          <div className='container'>
              <button className='back-button' onClick={goBack}>
                <svg width="31" height="31" viewBox="0 0 31 31" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M17.8228 24.4859C17.6302 24.4866 17.44 24.4441 17.2661 24.3616C17.0921 24.279 16.9388 24.1586 16.8176 24.0091L10.593 16.2767C10.4034 16.0461 10.2998 15.7568 10.2998 15.4583C10.2998 15.1598 10.4034 14.8706 10.593 14.64L17.0366 6.9076C17.2554 6.64442 17.5697 6.47892 17.9105 6.4475C18.2513 6.41608 18.5906 6.52132 18.8537 6.74007C19.1169 6.95882 19.2824 7.27315 19.3139 7.61393C19.3453 7.9547 19.24 8.294 19.0213 8.55718L13.2607 15.4648L18.828 22.3724C18.9856 22.5616 19.0857 22.7919 19.1164 23.0362C19.1472 23.2805 19.1074 23.5284 19.0016 23.7508C18.8959 23.9731 18.7287 24.1605 18.5197 24.2908C18.3108 24.4211 18.069 24.4888 17.8228 24.4859Z" fill="#231F20"/>
                </svg>
                <span>Back</span>
              </button>
              <h1>Self-reflection for <br/> Personal Growth</h1>
              <div>
                  <h2>Why self-reflection?</h2>
              </div>

              <div className="text-area-container">
                  <Carousel breakPoints={breakPoints}>
                      {items.map((item, index) => (
                          <fieldset key={index}>
                            <legend className='legend' id={'legend-' +  index}>{item.question}</legend>
                            <textarea className="input" id={'input-' + index} name="myInput" rows="10" cols="50"></textarea>
                          </fieldset>
                      ))}
                  </Carousel>
                <button className="save-button" onClick={downloadTxtFile} data-tooltip-id="my-tooltip" data-tooltip-content="Hello world!">Save answers</button>
              </div>
              {(mood !== 'explore') ?  (<button className='explore-button' onClick={goToExplore}>
                  GO TO EXPLORE
              </button> ) : (
                <button className='explore-button' onClick={goToHomepage}>
                  INPUT MOOD
                </button>
              )}
          </div>
          <Tooltip place="bottom" type="info" effect="solid" id="my-tooltip" />
        </div>
      );
}